<template>
  <div :id="this.task.id">
    <div
      v-if="task.redacting == false"
      class="Task"
      :class="{ completedTaskText: task.completed }">
      <img  src="@/assets/handle.png" class="handle" >   
      <div class="task-text">
        <span class="task-text-span">{{ task.task }}</span>
      </div>
      <div class="task-manager">
        <span>
          <button @click="deleteTask" class="task-btn">
            <img src="@/assets/delete.svg" alt="delete" />
          </button>
          <button @click="editTask" class="task-btn btn-edit">
            <img src="@/assets/edit.png" alt="edit" />
          </button>
        </span>
        <input
          class="circleRadius"
          type="checkbox"
          @click="changeTaskState($event)"
          :checked="task.completed"
        />
      </div>
    </div>
    <EDITTASK v-else :task="task"></EDITTASK>
  </div>
</template>

<script>
import EDITTASK from "/src/components/input-edit.vue";

export default {
  data() {
    return {
      
    };
  },
  props: {
    task: Object,
    indexInTasks: Number,
    indexInCompleted:Number,
    tasks: Array,
    completedTasks:Array,
  },
  methods: {
    changeTaskState: function() {
      this.task.completed = !this.task.completed;
      if(this.task.completed == true){
      let taskToMove = this.tasks.splice(this.indexInTasks,1)[0];
      this.completedTasks.push(taskToMove);
      } else if(this.task.completed == false){
      let taskToMove = this.completedTasks.splice(this.indexInCompleted,1)[0];
      console.log(taskToMove);
      this.tasks.push(taskToMove);
      }
    },

    deleteTask: function() {
        if(this.task.completed == false){
            
            this.tasks.splice(this.indexInTasks,1);

        }
        if(this.task.completed == true){
            
            this.completedTasks.splice(this.indexInCompleted,1);

        }
        
    },

    editTask: function() {
      this.task.redacting = true;
      this.$emit("setfocus");
    },
  },

  components: {
    EDITTASK,
  },
};
</script>

