<template>
  <div class="wrapper">
     <div class="description">
        <form class="new-tasks" action="">
          <input id="new-tasks-input" type="text" placeholder="Write a new task" />
          <button class="addNew" @click.prevent="addNewTask">add</button>
        </form>
      </div>
    <div class="tasks ">
     
      <div class="tasks-field">
        <draggable
          v-model="tasks"
          group="tasks"
          @start="drag = true"
          @end="drag = false"
          v-bind="dragOptions"
          handle=".handle"
        >
          <TASK
            v-for="(task, index) in tasks"
            :key="task.id"
            :task="task"
            :tasks="tasks"
            :indexInTasks="index"
            :indexInCompleted="index"
            :completedTasks="completedTasks"
            
          >
          </TASK>
        </draggable>
        <draggable
          v-model="completedTasks"
          group="completed"
          @start="drag = true"
          @end="drag = false"
          v-bind="dragOptions"
          handle=".handle"
        >
        <TASK
            v-for="(task, index) in completedTasks"
            :key="task.id"
            :task="task"
            :tasks="tasks"
            :indexInTasks="index"
            :indexInCompleted="index"
            :completedTasks="completedTasks"
            
          >
          </TASK>
          </draggable>
      </div>
    </div>
  </div>
</template>

<script>
import TASK from "/src/components/Task.vue";
import draggable from "vuedraggable";

export default {
  name: "App",
  data() {
    return {
      tasks: [],
      completedTasks:[],
    };
  },

  ///////////////////////////////////////////////////

  methods: {
    addNewTask: function() {
      let input = document.getElementById("new-tasks-input");
      let taskText = input.value;
      if (taskText == "") return;
      this.tasks.push({
        task: taskText,
        completed: false,
        id: this.newID,
        redacting: false,
        showControlMeny: false,
      });
      input.value = "";
      //input.blur();
    },
    
  },

  /////////////////////////////////////////////

  computed: {
    newID: function() {
      let id = -1;
      this.tasks.forEach((task) => {
        if (task.id > id) id = task.id;
      });
      this.completedTasks.forEach((task) => {
        if (task.id > id) id = task.id;
      });

      return id + 1;
    },
    dragOptions() {
      return {
        animation: 200,
        group: "description",
        disabled: false,
        ghostClass: "ghost",
      };
    },
  },

  ////////////////////////////////////////////////////////////////

  components: {
    TASK,
    draggable,
  },
  mounted: function() {
    if (localStorage.todoAplicationTasks) {
      this.tasks = JSON.parse(localStorage.todoAplicationTasks);
      this.tasks.forEach((el) => {
        el.redacting = false;
      });
    }
    if (localStorage.todoAplicationCompletedTasks) {
      this.completedTasks = JSON.parse(localStorage.todoAplicationCompletedTasks);
      this.completedTasks.forEach((el) => {
        el.redacting = false;
      });
    }
  },

  ////////////////////////////////////////////////////////////////////////

  watch: {
    tasks: {
      handler: function(newValue) {
        localStorage.todoAplicationTasks = JSON.stringify(newValue);
      },
      deep: true,
    },
    completedTasks: {
      handler: function(newValue) {
        localStorage.todoAplicationCompletedTasks = JSON.stringify(newValue);
      },
      deep: true,
    },
  },
};
</script>

